#body{
  background-color: rgb(255, 249, 245);
}

#wrapper{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

#fakePic{
  width: 42vw;
  height: 42vw;
  margin: 3vw;
  background-color: black;
  display: inline-block;
}

#content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 42vw;
}

#heading{
  /* font-size: 50px; */
  line-height: 100px;
  color: black;
  margin: 20px 0; 
  font-weight: 700;
}

#textDiv{
  font-size: 14px;
  line-height: 28px;
}

#linkMore{
  line-height: 100px;
  color: black;
  font-weight: 700;
}

#linkMore:hover{
  color: rgb(184, 144, 144);
}

#hambi{
  width: 74px;
  height: 74px;
  border-radius: 50%;
  position: fixed;
  right: 20px;
  top: 20px;
  padding: 0;
  background-color: #343A40;
  /* transition: all 0.5s ease-in-out; */
}

#hambi.open{
  background-color: rgb(136, 135, 135);
}

.hambiLine{
  width: 40px;
  height: 5px;
  margin: 0 auto;
  background-color: rgb(255, 249, 245);
  position: relative;
}

#hL1{
  top: 22px;
}

#hL2{
  top: 30px;
}

#hL3{
  top: 38px;
}

#hambiMenu{
  position: fixed;
  top: 100px;
  right: 20px;
  height: 200px;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: rgb(136, 135, 135);
  color: white;
  border-radius: 10px;
  font-weight: 500;
  /* transition: all 0.5s ease-in-out; */
}

#hambi:hover{
  background-color: rgb(184, 144, 144);
}

.hambiMenuItem{
  height: 30%;
  align-content: center;
  padding: 20px;
}

.hambiMenuItem:hover{
  background-color: rgb(184, 144, 144);
}

.hambiMenuItem a:hover{
  text-decoration: none;
}

.hambiMenuItem a{
  color: white;
}




@media screen and (max-width: 600px){

  #fakePic{
    width: 100vw;
    height: 100vw;
    margin: 0;
  }

  #content{
    width: 80vw;
    margin: auto
  }

  #hambi{
    width: 60px;
    height: 60px;
    border: 1px solid rgb(255, 249, 245);
  }
  
  #hambi.open{
    background-color: rgb(136, 135, 135);
  }
  
  .hambiLine{
    width: 30px;
    height: 3px;
  }
  
  #hL1{
    top: 18px;
  }
  
  #hL2{
    top: 24px;
  }
  
  #hL3{
    top: 30px;
  }
}


